@import "../../../css/module/fn";
@import "../../../css/module/color";

.wrap{
	margin: p2m(40);
	border-radius: p2m(15);
	overflow: hidden;
	@include shadow;
	
	
	#btnSelect{
		background-color: $red;
		color: white;
		width: 100%;
		height:p2m(110);
	}
	table{
		width: 100%;
		background-color: #fff;
		table-layout: fixed;
		
		&.list {
			tr:nth-of-type(odd){
				background-color: #eee;
			}
		}
		tr{
			height: p2m(85);
			line-height: p2m(85);
			th{
				font-weight: 900;
			}
			td,th{
				font-size: p2m(24);
				text-align: center;
			}
		}
	}
	
}